<template>
  <view class="register-container">
    <view class="register-header">
      <text class="title">Áî®Êà∑Ê≥®ÂÜå</text>
    </view>
    
    <view class="register-form">
      <view class="form-item">
        <text class="label">Áî®Êà∑Âêç</text>
        <input 
          class="input" 
          v-model="form.username" 
          placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÔºà3-20‰Ωç,‰∏çÂèØÊõ¥ÊîπÔºâ"
          maxlength="20"
        />
      </view>
      
      <view class="form-item input-with-suffix">
        <text class="label">ÂØÜÁ†Å</text>
        <view class="input-wrapper">
          <input 
            class="input" 
            v-model="form.password" 
            :password="!regPwdVisible"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†ÅÔºà6-20‰ΩçÔºâ"
            maxlength="20"
          />
          <view class="suffix-group">
            <view class="clear-toggle" v-if="form.password" @click.stop="form.password = ''">
              <text class="icon-clear">‚úï</text>
            </view>
            <view class="eye-toggle" @click.stop="regPwdVisible = !regPwdVisible">
              <text class="icon-eye">{{ regPwdVisible ? 'üëÅÔ∏è' : 'üôà' }}</text>
            </view>
          </view>
        </view>
      </view>
      
      <view class="form-item input-with-suffix">
        <text class="label">Á°ÆËÆ§ÂØÜÁ†Å</text>
        <view class="input-wrapper">
          <input 
            class="input" 
            v-model="form.confirmPassword" 
            :password="!regConfirmVisible"
            placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"
            maxlength="20"
          />
          <view class="suffix-group">
            <view class="clear-toggle" v-if="form.confirmPassword" @click.stop="form.confirmPassword = ''">
              <text class="icon-clear">‚úï</text>
            </view>
            <view class="eye-toggle" @click.stop="regConfirmVisible = !regConfirmVisible">
              <text class="icon-eye">{{ regConfirmVisible ? 'üëÅÔ∏è' : 'üôà' }}</text>
            </view>
          </view>
        </view>
      </view>
      
      <!-- ÈöêÁßÅÊîøÁ≠ñÂêåÊÑèÂãæÈÄâ -->
      <view class="form-item privacy-row">
        <view class="checkbox" :class="{ checked: agreePrivacy }" @click="toggleAgree">
          <text v-if="agreePrivacy">‚úî</text>
        </view>
        <text class="privacy-tip">ÊâìÂãæ‰ª£Ë°®ÂêåÊÑè</text>
        <text class="privacy-link" @click="navigateToPrivacy">ÈöêÁßÅÊîøÁ≠ñ</text>
      </view>
      
      <button class="register-btn" @click="handleRegister" :disabled="loading">
        {{ loading ? 'Ê≥®ÂÜå‰∏≠...' : 'Ê≥®ÂÜå' }}
      </button>
      
      <view class="login-tip">
        Â∑≤ÊúâË¥¶Âè∑Ôºü<text class="login-link" @click="navigateToLogin">ÂéªÁôªÂΩï</text>
      </view>

      <!-- Ê≥®ÂÜåÂâçÁ°ÆËÆ§ÂºπÁ™óÔºà3ÁßíÂÄíËÆ°Êó∂ÂêéÊñπÂèØÁ°ÆËÆ§Ôºâ -->
      <view v-if="showConfirmDialog" class="modal-mask">
        <view class="modal-dialog">
          <view class="modal-title">ÈáçË¶ÅÊèêÈÜí</view>
          <view class="modal-content">
            ËØ∑Êà™Âõæ‰øùÂ≠òÁî®Êà∑Âêç‰∏éÂØÜÁ†ÅÔºåÁî®Êà∑Âêç‰∏çÂèØÊõ¥Êîπ
          </view>
          <view class="modal-actions">
            <button class="btn-cancel" @click="cancelConfirm" :disabled="confirmLoading">ÂèñÊ∂à</button>
            <button class="btn-ok" @click="confirmRegister" :disabled="!confirmEnabled" :class="{ disabled: !confirmEnabled }">
              {{ confirmEnabled ? 'Á°ÆËÆ§' : `Á°ÆËÆ§Ôºà${confirmCountdown}sÔºâ` }}
            </button>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
        confirmPassword: ''
      },
      loading: false,
      regPwdVisible: false,
      regConfirmVisible: false,
      agreePrivacy: false,
      policyVersion: '1.0.0',
      // Á°ÆËÆ§ÂºπÁ™óÊéßÂà∂
      showConfirmDialog: false,
      confirmCountdown: 3,
      confirmEnabled: false,
      confirmTimer: null,
      confirmLoading: false
    }
  },
  methods: {
    validateForm() {
      if (!this.form.username.trim()) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç',
          icon: 'none'
        });
        return false;
      }
      
      if (this.form.username.trim().length < 3) {
        uni.showToast({
          title: 'Áî®Êà∑ÂêçÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é3‰Ωç',
          icon: 'none'
        });
        return false;
      }
      
      if (!this.form.password) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å',
          icon: 'none'
        });
        return false;
      }

      
      
      if (this.form.password.length < 6) {
        uni.showToast({
          title: 'ÂØÜÁ†ÅÈïøÂ∫¶‰∏çËÉΩÂ∞ë‰∫é6‰Ωç',
          icon: 'none'
        });
        return false;
      }
      
      if (this.form.password !== this.form.confirmPassword) {
        uni.showToast({
          title: '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥',
          icon: 'none'
        });
        return false;
      }
      // ÂøÖÈ°ªÂãæÈÄâÈöêÁßÅÊîøÁ≠ñ
      if (!this.agreePrivacy) {
        uni.showToast({
          title: 'ËØ∑ÂãæÈÄâÂπ∂ÂêåÊÑèÈöêÁßÅÊîøÁ≠ñ',
          icon: 'none'
        });
        return false;
      }
      
      return true;
    },
    
    handleRegister() {
      if (!this.validateForm()) return;
      // ÊâìÂºÄÁ°ÆËÆ§ÂºπÁ™óÂπ∂ÂºÄÂßã 3 ÁßíÂÄíËÆ°Êó∂
      this.openConfirmDialog();
    },

    openConfirmDialog() {
      this.showConfirmDialog = true;
      this.confirmEnabled = false;
      this.confirmLoading = false;
      this.confirmCountdown = 3;
      // Ê∏ÖÁêÜÊóßËÆ°Êó∂Âô®
      if (this.confirmTimer) {
        clearInterval(this.confirmTimer);
        this.confirmTimer = null;
      }
      this.confirmTimer = setInterval(() => {
        if (this.confirmCountdown > 1) {
          this.confirmCountdown -= 1;
        } else {
          this.confirmCountdown = 0;
          this.confirmEnabled = true;
          clearInterval(this.confirmTimer);
          this.confirmTimer = null;
        }
      }, 1000);
    },

    cancelConfirm() {
      if (this.confirmTimer) {
        clearInterval(this.confirmTimer);
        this.confirmTimer = null;
      }
      this.showConfirmDialog = false;
      this.confirmEnabled = false;
      this.confirmLoading = false;
    },

    async confirmRegister() {
      if (!this.confirmEnabled || this.confirmLoading) return;
      this.confirmLoading = true;
      await this.doRegister();
      this.cancelConfirm();
    },

    async doRegister() {
      try {
        this.loading = true;
        // Ë∞ÉÁî®‰∫ëÂáΩÊï∞ËøõË°åÊ≥®ÂÜå
        const res = await uniCloud.callFunction({
          name: 'userRegister',
          data: {
            username: this.form.username.trim(),
            password: this.form.password,
            agreePrivacy: this.agreePrivacy,
            policyVersion: this.policyVersion
          },
          timeout: 10000
        });
        if (res.result.code === 0) {
          uni.showToast({ title: 'Ê≥®ÂÜåÊàêÂäü', icon: 'success' });
          setTimeout(() => {
            uni.redirectTo({ url: '/pages/login/login' });
          }, 1500);
        } else if (res.result.code === 409) {
          uni.showToast({ title: res.result.message || 'Áî®Êà∑ÂêçÈáçÂ§ç', icon: 'none' });
          return;
        } else {
          throw new Error(res.result.message || 'Ê≥®ÂÜåÂ§±Ë¥•');
        }
      } catch (err) {
        console.error('Ê≥®ÂÜåÂ§±Ë¥•:', err);
        uni.showToast({ title: err.message || 'Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        this.loading = false;
        this.confirmLoading = false;
      }
    },
    
    navigateToLogin() {
      // ‰ΩøÁî® redirectTo ÊõøÊç¢ÂΩìÂâçÈ°µÔºåÈÅøÂÖçÁôªÂΩïÊàêÂäüÂêé navigateBack ÂõûÂà∞Ê≥®ÂÜåÈ°µ
      uni.redirectTo({
        url: '/pages/login/login'
      });
    },
    navigateToPrivacy() {
      uni.navigateTo({ url: '/pages/privacy/privacy' });
    },
    toggleAgree() {
      this.agreePrivacy = !this.agreePrivacy;
    }
  }
}
</script>

